body {
    margin: 0;
    padding: 0;
    font-family: "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif;
    letter-spacing: 0.08em;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: black;
    color: white;
    cursor: default;
}
h2 {
    text-align: center;
    padding-top: 0.8%;
    padding-bottom: 3%; 
    font-weight: 300;
    font-size: 16px;
    letter-spacing: 0.1em;
    color: white;
}

.author {
    font-size: 9px;
    font-weight: 200;
    letter-spacing: 0.1em;
    position: absolute;
    bottom: 0.8%;
    right: 1.6%;
}
.list {
    margin: 0 2% 0 2%;
    cursor: default;
    width: 96%;
    height: 70%;
    /* background-color : gray; */
    display: flex;
    justify-content: center;
    line-height: 1.7em;
    font-size: 18px;
}

.content {
    padding: 2px;
}

.content :hover {
    color: #5200ff;
}

.content-wrapper {
    width: 40%;
    margin: 0 16px 0 16px;
}


.on {
    color: #5200ff;
}

.off {
    color: white;
}
.page-wrapper {
    margin: 0 2% 0 2%;
    width: 96%;
    height: 20%;
}

.page {
    padding-top: 3%;
    display: flex;
    justify-content: center;
    /* background-color: gray; */
}

.intro {
    margin: 7% 2% 0 2%;
    width: 96%;
    text-align: center;
    font-size: 14px;
    /* background-color: blue;  */
}

.page p {
    font-size: 12px;
}

.content-name {
    position: absolute;
    left: 12%;
    /* background-color : gray; */
}

#caption {
    height: 54px;
    padding: 8px;
    margin: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.original {
    text-align: center;
    width: 24%;
}

.representation {
    text-align: center;
    width: 24%;
}

.interpolation {
    text-align: center;
    width: 24%;
}

audio {
    padding: 8px;
}

a, a:link, a:visited, i {
    color : gray;
    text-decoration: none;
}
